import React, { useEffect, useMemo } from 'react';
import Select from 'react-select';
import { useSelector } from 'react-redux';
import CustomTextField from '../../../../Layout/CustomTextField';
import countryList from 'react-select-country-list';
import { renderErrorTxt } from '../../../../../../utils/validations/Investor-Application-Form/formErrorUtils';

function PublicListedComp({ secE, onChangeFn }) {

    const invFormStatus = useSelector((store) => store.invFormStatus);

    const options = useMemo(() => countryList().getData(), []);

    useEffect(()=>{
        return ()=> {
            onChangeFn({name:'public_company_market_name', value:''})
            onChangeFn({name:'public_company_country', value:''})
        }
    },[]);

    const countryValue = () => {
        const value = options.find((country) => {
            if(secE?.regulatory_detail?.public_company_country){
              return  country.label?.toLowerCase() === secE?.regulatory_detail?.public_company_country.toLowerCase()
            }
            return false
        });
        return value || null;
    }

    const onChangeSelectHandler = (value) => {
        onChangeFn({ name: 'public_company_country', value: value.label })
    }

  return (
    <div className="sub__form u-padding-y">
        <CustomTextField
            placeholder="Name of market / exchange"
            id="sec-e-public-listed-name"
            label="Name of market / exchange"
            asterisk="*"
            name="public_company_market_name"
            value={secE.regulatory_detail?.public_company_market_name || ""}
            onChangeFn={onChangeFn}
            formErrors={invFormStatus.errors}
        />

        <div className="country__dropdown">
            <label>Country <span>*</span></label>
            <Select
                className={invFormStatus.errors?.public_company_country && 'incorrect'}
                id="select-dropdown-country"
                name="public_company_country"
                autoFocus={false}
                options={options}
                placeholder="Select a Country"
                value={countryValue()}
                onChange={onChangeSelectHandler}
            />

            {invFormStatus.errors?.public_company_country?
            <div className="input__desc">
                {renderErrorTxt(invFormStatus.errors?.public_company_country)}
            </div>: null}
        </div>
    </div>
  )
}

export default PublicListedComp