import { useSelector } from 'react-redux';
import { COMPANY_TYPE_PUBLIC_E, COMPANY_TYPE_PROPRIETARY_E } from '../../../../../constants';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';

const CompanyType = ({onFormChange}) => {
  const secE = useSelector((store)=> store.secE);
  const invFormStatus = useSelector((store) => store.invFormStatus)

  const onChangeRadioFn = ({name, value}) =>{ 
    const convrtToNum = parseInt(value);
    onFormChange({name, value:convrtToNum});
  }

  return (
    <div className="section__container">
      <h4 className="section__container--subtitle">E.2 Company Type</h4>
        <div className="section__container--box">
        <h6>Is the company type public or proprietary?<span>*</span></h6>
          <CustomRadioInput
            name="company_type"
            id="sec-e-company-type-public"
            content="Public (companies whose name does NOT include the words Pty Ltd or Proprietary Ltd; generally listed companies) Go to Section E.4"
            type="row-input"
            val={COMPANY_TYPE_PUBLIC_E}
            checked={secE?.company_type === COMPANY_TYPE_PUBLIC_E}
            onChangeFn={onChangeRadioFn}
          />
          <CustomRadioInput
            name="company_type"
            id="sec-e-company-type-proprietary"
            content="Proprietary (companies whose name ends with Proprietary Ltd or Pty Ltd; also known as private company"
            type=""
            val={COMPANY_TYPE_PROPRIETARY_E}
            checked={secE?.company_type === COMPANY_TYPE_PROPRIETARY_E}
            onChangeFn={onChangeRadioFn}
          />
          {invFormStatus.errors?.company_type? <div className="input__desc">
            {renderErrorTxt(invFormStatus.errors?.company_type)}
          </div>: null}
        </div>
    </div>
  )
}

export default CompanyType;