import React, {useState} from 'react';
import { useSelector } from 'react-redux';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import DocPlaceholder from '../../../Layout/DocPlaceholder';
import { uploadFiles, deleteFile } from '../../../../../actions/Investor-Application-Form/uploadDocActions';
import { LOADING, SUCCESS, FAILED, IDENTIFICATION_DOCUMENTS_SEC_E, ASIC_EXTRACT_DOC, COMPANY_OR_FOREIGN_BODY_EXTRACT, SEC_E_DOC_EXTRA_LABEL_MAIN } from '../../../../../constants';
import CustomDropdown from '../../../Layout/CustomDropdown';
import { addNewDocToArray, removeDocFromArray, updateDocArray } from '../../../../../utils/documentUploads';
import { useToast } from '../../../../CustomToast';

const IdentificationDocs = ({investorFormId}) => {
    const dummyFn = () => {}
    const toast = useToast();
    const doNotShowExtraLabel = [ASIC_EXTRACT_DOC[0][0], COMPANY_OR_FOREIGN_BODY_EXTRACT[0][0]]
    
    const secE = useSelector((store) => store.secE);
    const [selectedDocType, setSelectedDocType] = useState({label:"Select type of document", value:""})
    const [selectedDirectorNum, setSelectedDirectorNum] = useState({label:`${SEC_E_DOC_EXTRA_LABEL_MAIN} - 1`, value:1})
    const [uploadedDocs, setUploadedDocs] = useState([])

    const onUploadFile = async(file, type={label:'', value:''}, extraDetails={label:'', value:''}) =>{ //call api to upload files
        if(file){                                 
            const fileName = file.name
            const metadata = JSON.stringify([{"owner":`${SEC_E_DOC_EXTRA_LABEL_MAIN} ${extraDetails.value}`}])

            const formData = new FormData();
            formData.append('file', file);
            formData.append('type', type.value);
            if(!doNotShowExtraLabel.includes(type.value)){
                formData.append("metadata", metadata)
            }
            formData.append('section', 'e');
            formData.append('investor_form_id', investorFormId);
            const { success, url, data } = await uploadFiles(formData);
            const extradata = {
                loader: success? SUCCESS : FAILED,
                docUrl: url,
                resData: data
            }
            updateDocArray({setUploadedDocs, fileName, extradata})
        }
    }

    const onAddFiles = (files) => {
        //add selected files(by user) to local state - and call api
        addNewDocToArray({
            files,
            uploadedDocs,
            setUploadedDocs,
            onUploadFile,
            doNotShowExtraLabel,
            selectedDocType:{...selectedDocType},
            selectedEntityNum:{...selectedDirectorNum},
        })
    }

    const onClickRemove = (fileName) => { 
        //just remove the file from local state
        removeDocFromArray({fileName, uploadedDocs, setUploadedDocs})
    }

    const updateSelectedOption = (option) => {
        //update selected document type to the state
        setSelectedDocType({...option})
    }

    const updateSelectedDirectorNum = (option) => {
        setSelectedDirectorNum({...option})
    }

    const onClickDel = async({fileName, docData}) => {
        //delete the file from aws & remove the file from local state
        updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: true}})
        const { success } = await deleteFile(docData?.resData);
        success && onClickRemove(fileName);
        success && toast.success("Deleted successfully!");
        success || updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: false}})
        success || toast.error("Please try again!")
    }

    const inestorOptions = () => {
        const tempOptionsArr = [];
        const tempNoOfDirectors = secE?.number_of_directors === -1? 4 : secE?.number_of_directors || 1
        for (let i = 1; i <= tempNoOfDirectors; i++) {
            tempOptionsArr.push([i, `${SEC_E_DOC_EXTRA_LABEL_MAIN} - ${i}`])
        }
        return tempOptionsArr
    }

return (
    <div className="section__container">
        <h4 className="section__container--subtitle">E.5 Identification Documents</h4>

        {/* Registered with ASIC */}
        <div className="section__container--box">
            <h5>If the company is registered with ASIC</h5>

            <div>
                <h6>Please provide a copy of ONE of the following documents.</h6>
                <ul>
                    <li>Full ASIC company extract (if company is registered in Australia)</li>
                    <li>Full company extract from relevant registration body</li>
                    <li>If the ASIC or foreign registration body database is not reasonably available, an original certified copy of the certificate of registration issued by ASIC or the relevant registration body</li>
                </ul>
            </div>
        </div>

        {/* NOT Registered with ASIC */}
        <div className="section__container--box">
            <h5>If the company is NOT registered with ASIC</h5>

            <div>
                <h6>Please provide a copy of ONE of the following documents.</h6>
                <ul>
                    <li>Full company extract from relevant registration body</li>
                    <li>If the foreign registration body database is not reasonably available, an original certified copy of the certificate of registration issued by the relevant registration body</li>
                </ul>
            </div>
        </div>

        {/* majority subsidiary */}
        <div className="section__container--box">
            <h5>If the company is regulated, listed or a majority subsidiary of an Australian listed company</h5>

            <div className="u-margin-bottom">
                <h6>Please provide a copy of ONE of the following documents</h6>
                <ul>
                    <li>Full ASIC company extract</li>
                    <li>Full company extract from relevant financial market</li>
                    <li>Evidence of the company's full name from the relevant Commonwealth, State or Territory statutory regulator</li>
                    <li>A public document issued by the company</li>
                </ul>
            </div>
            
            <div className="note__box u-padding-y">
                <p className="note__box--p">*Documents that are written in a language other than English must be accompanied with an English translation prepared by an Accredited Translator.
    If you are unable to provide one of the above documents, please contact SS&amp;C on 02 8115 9924 or ISTradedesk@sscinc.com</p>
            </div>
        </div>

        <div className="section__container--box">
            <h5>For each Beneficial Owner (as defined in Page 19)</h5>
            
            {/* Copy of ONE beneficial owner */}
            <div className="u-padding-y">
                <h6>1. Provide an ORIGINAL CERTIFIED COPY of ONE of the following documents for each Beneficial Owner:</h6>
                <ul>
                    <li>Australian State / Territory driver's licence containing a photograph of the person</li>
                    <li>Australian passport (a passport that has expired within the preceding 2 years is acceptable)</li>
                    <li>Card issued under a State or Territory for the purpose of proving a person's age containing a photograph of the person</li>
                </ul>
            </div>

            <div className="divider-or u-margin-bottom"></div>

            {/* Copy of TWO beneficial owner */}
            <div className="u-padding-10y">
                <h6>2. Provide an ORIGINAL CERTIFIED COPY of TWO of the following documents for each Beneficial Owner:</h6>
                <ul>
                    <li>Foreign passport or similar travel document containing a photograph and the signature of the person*</li>
                    <li>Foreign driver's licence that contains a photograph of the person in whose name the document is issued*</li>
                    <li>National ID card issued by a foreign government containing a photograph and a signature of the person in whose name the card was issued*</li>
                </ul>
                <div className="note__box u-padding-10y">
                    <p className="note__box--p">*Documents that are written in a language other than English must be accompanied with an English translation prepared by an Accredited Translator. If you are unable to provide one of the above documents, please contact SS&amp;C on 02 8115 9924 or ISTradedesk@ sscinc.com</p>
                </div>
            </div>

            <div>
                <h6 className="u-margin-bottom u-padding-y">Please attach documents</h6>
                <div className="sub__form" style={{gap:16}}>
                    <div style={{minWidth: 350}}>
                        <CustomDropdown 
                            dropdownOptions={inestorOptions()}
                            selectedOption={selectedDirectorNum}
                            updateSelectedOption={updateSelectedDirectorNum}
                        />
                    </div>
                    <div style={{minWidth: 375}}>
                        <CustomDropdown
                            addClass='control-dropdown-height'
                            dropdownOptions={IDENTIFICATION_DOCUMENTS_SEC_E}
                            selectedOption={selectedDocType}
                            updateSelectedOption={updateSelectedOption}
                            extraLabel={`(${SEC_E_DOC_EXTRA_LABEL_MAIN} - ${selectedDirectorNum?.value})`}
                            doNotShowExtraLabelSpecial={doNotShowExtraLabel}
                        />
                    </div>
                </div>
                {selectedDocType?.value ?
                    <div className='u-padding-y'>
                        <DocPlaceholder onAddFiles={onAddFiles} uploadedDocs={uploadedDocs} onClickRemove={onClickRemove} onClickDel={onClickDel} uniqId="identificationdocs_e"/>
                    </div>: null
                }
            </div>
        </div>
    </div>
  )
}

export default IdentificationDocs;