import React, {useEffect} from 'react';
import { useSelector } from 'react-redux';
import CustomTextField from '../../../../Layout/CustomTextField';

function RegulatedComp({secE, onChangeFn}) {
    const invFormStatus = useSelector((store) => store.invFormStatus)

    useEffect(()=>{
        return ()=> {
            onChangeFn({name:'regulator_name', value:''})
            onChangeFn({name:'regulator_licence_details', value:''})
        }
    },[])

    return (
    <div className="sub__form u-padding-y">
        <CustomTextField 
            placeholder="Regulator name"
            id="sec-e-regulator-name"
            label="Regulator name"
            asterisk="*"
            name="regulator_name"
            value={secE.regulatory_detail?.regulator_name || ""}
            onChangeFn={onChangeFn}
            formErrors={invFormStatus.errors}
        />
        <CustomTextField 
            placeholder="Licence details (e.g. AFSL, ACL, RSE)e"
            id="sec-e-regulator-licence"
            label="Licence details (e.g. AFSL, ACL, RSE)"
            asterisk="*"
            name="regulator_licence_details"
            value={secE.regulatory_detail?.regulator_licence_details || ""}
            onChangeFn={onChangeFn}
            formErrors={invFormStatus.errors}
        />
    </div>
  )
}

export default RegulatedComp