import React, { useMemo } from 'react';
import { useSelector } from 'react-redux';
import Select from 'react-select';
import CustomTextField from '../../../Layout/CustomTextField';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import RegistrationBodyInp from '../RegistrationBodyInp';
import NonAsicIdentNum from '../NonAsicIdentNum';
import { ADDRESS_TYPE_AUSTRALIAN_E, ADDRESS_TYPE_ASIC_REGISTERED_E, ADDRESS_TYPE_NON_ASIC_REGISTERED_E, YES_E, NO_E } from '../../../../../constants';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import countryList from 'react-select-country-list';
import { replaceAllWhiteSpaces } from '../../../../../utils/regexFuncs';

const ForeignCompanyDetails = ({ onFormChange }) => {

    const secE = useSelector((store)=> store.secE);
    const invFormStatus = useSelector((store) => store.invFormStatus)

    const options = useMemo(() => countryList().getData(), []);

    const errMsgsAus = {
        full_company_address : invFormStatus.errors?.full_company_address? invFormStatus.errors?.full_company_address[ADDRESS_TYPE_AUSTRALIAN_E]: [],
        unit_no : invFormStatus.errors?.unit_no? invFormStatus.errors?.unit_no[ADDRESS_TYPE_AUSTRALIAN_E]: [],
        street_no : invFormStatus.errors?.street_no? invFormStatus.errors?.street_no[ADDRESS_TYPE_AUSTRALIAN_E]: [],
        street_name : invFormStatus.errors?.street_name? invFormStatus.errors?.street_name[ADDRESS_TYPE_AUSTRALIAN_E]: [],
        suburb : invFormStatus.errors?.suburb? invFormStatus.errors?.suburb[ADDRESS_TYPE_AUSTRALIAN_E]: [],
        state : invFormStatus.errors?.state? invFormStatus.errors?.state[ADDRESS_TYPE_AUSTRALIAN_E]: [],
        postcode : invFormStatus.errors?.postcode? invFormStatus.errors?.postcode[ADDRESS_TYPE_AUSTRALIAN_E]: [],
        countrySub : invFormStatus.errors?.countrySub? invFormStatus.errors?.countrySub[ADDRESS_TYPE_AUSTRALIAN_E]: [],
    }
    const errMsgsAsic = {
        full_company_address : invFormStatus.errors?.full_company_address? invFormStatus.errors?.full_company_address[ADDRESS_TYPE_ASIC_REGISTERED_E]: [],
        unit_no : invFormStatus.errors?.unit_no? invFormStatus.errors?.unit_no[ADDRESS_TYPE_ASIC_REGISTERED_E]: [],
        street_no : invFormStatus.errors?.street_no? invFormStatus.errors?.street_no[ADDRESS_TYPE_ASIC_REGISTERED_E]: [],
        street_name : invFormStatus.errors?.street_name? invFormStatus.errors?.street_name[ADDRESS_TYPE_ASIC_REGISTERED_E]: [],
        suburb : invFormStatus.errors?.suburb? invFormStatus.errors?.suburb[ADDRESS_TYPE_ASIC_REGISTERED_E]: [],
        state : invFormStatus.errors?.state? invFormStatus.errors?.state[ADDRESS_TYPE_ASIC_REGISTERED_E]: [],
        postcode : invFormStatus.errors?.postcode? invFormStatus.errors?.postcode[ADDRESS_TYPE_ASIC_REGISTERED_E]: [],
        countrySub : invFormStatus.errors?.countrySub? invFormStatus.errors?.countrySub[ADDRESS_TYPE_ASIC_REGISTERED_E]: [],
    }
    const errMsgsNonAsic = {
        full_company_address : invFormStatus.errors?.full_company_address? invFormStatus.errors?.full_company_address[ADDRESS_TYPE_NON_ASIC_REGISTERED_E]: [],
        unit_no : invFormStatus.errors?.unit_no? invFormStatus.errors?.unit_no[ADDRESS_TYPE_NON_ASIC_REGISTERED_E]: [],
        street_no : invFormStatus.errors?.street_no? invFormStatus.errors?.street_no[ADDRESS_TYPE_NON_ASIC_REGISTERED_E]: [],
        street_name : invFormStatus.errors?.street_name? invFormStatus.errors?.street_name[ADDRESS_TYPE_NON_ASIC_REGISTERED_E]: [],
        suburb : invFormStatus.errors?.suburb? invFormStatus.errors?.suburb[ADDRESS_TYPE_NON_ASIC_REGISTERED_E]: [],
        state : invFormStatus.errors?.state? invFormStatus.errors?.state[ADDRESS_TYPE_NON_ASIC_REGISTERED_E]: [],
        postcode : invFormStatus.errors?.postcode? invFormStatus.errors?.postcode[ADDRESS_TYPE_NON_ASIC_REGISTERED_E]: [],
        countrySub : invFormStatus.errors?.countrySub? invFormStatus.errors?.countrySub[ADDRESS_TYPE_NON_ASIC_REGISTERED_E]: [],
    }

    const onChangeRadioFn = ({name, value}) =>{ 
        const convrtToNum = parseInt(value);
        onFormChange({name, value:convrtToNum});
    }

    const onChangeFn = ({name, value}) =>{
        onFormChange({name, value})
    }

    const onChangeARBN = ({name, value}) =>{
        const trimmedVal = replaceAllWhiteSpaces(value)
        onChangeFn({name, value:trimmedVal})
    }

    const onChangeAddress = ({name, value, addressType}) =>{
        onFormChange({name, value, level:"address_details", address_type:addressType})
    }

    const getValueAddressType = (addressType, keyName) =>{
        const address = secE.address_details.find((each)=> addressType===each?.address_type) || {};
        const val = address[keyName] || ""
        return val
    }

    const countryValue = (type) => {
        
        const findCountry = getValueAddressType(type, "country");

        const value = options.find((country) => {
            if(findCountry){
              return  country.label?.toLowerCase() === findCountry?.toLowerCase();
            }
            return false
        });
        return value || null;
    }


  return (
    <div className="section__container">
        <h4 className="section__container--subtitle">E.1 Foreign Company Details</h4>

        <div className="section__container--box">
            <div>
                <CustomTextField 
                    placeholder="Full name of the foreign company"
                    id="sec-e-fullname-fc"
                    label="Full name of the foreign company"
                    type="form-input-full-width"
                    asterisk="*"
                    name="full_name"
                    value={secE?.full_name}
                    onChangeFn={onChangeFn}
                    formErrors={invFormStatus.errors}
                />
                <CustomTextField
                    placeholder="Country of formation / incorporation / registration"
                    id="sec-e-country-of-formation"
                    label="Country of formation / incorporation / registration"
                    type="form-input-full-width"
                    asterisk="*"
                    name="country"
                    value={secE?.country}
                    onChangeFn={onChangeFn}
                    formErrors={invFormStatus.errors}
                />
            </div>

            {/* Complete this section if the company is registered with ASIC */}
            <div>
                <h5 className="u-padding-10y">Complete this section if the company is registered with ASIC</h5>
                <div className="sub__form">
                    <CustomTextField
                        placeholder="ARBN"
                        id="sec-e-arbn"
                        label="ARBN"
                        asterisk=""
                        name="arbn"
                        value={secE?.arbn}
                        onChangeFn={onChangeARBN}
                        formErrors={invFormStatus.errors}
                    />
                    <CustomTextField
                        placeholder="Australian agent name"
                        id="sec-e-name-aus-agent"
                        label="Australian agent name (if applicable)"
                        asterisk=""
                        name="australian_agent_name"
                        value={secE?.australian_agent_name}
                        onChangeFn={onChangeFn}
                        formErrors={invFormStatus.errors}
                    />
                </div>
                <div className="u-padding-10y">
                    <CustomTextField
                        placeholder="Principal place of business in Australia or Australian agent address details"
                        id="sec-e-place-of-business"
                        label="Principal place of business in Australia or Australian agent address details (PO Box not permitted)"
                        asterisk=""
                        type="form-input-full-width"
                        name="full_company_address"
                        value={getValueAddressType(ADDRESS_TYPE_AUSTRALIAN_E, "full_company_address")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_AUSTRALIAN_E})}
                        formErrors={errMsgsAus}
                    />
                </div>
                <div className="sub__form">
                    <CustomTextField
                        placeholder="Unit No./Level"
                        id="sec-e-unit-no"
                        label="Unit No./Level"
                        asterisk=""
                        name="unit_no"
                        value={getValueAddressType(ADDRESS_TYPE_AUSTRALIAN_E, "unit_no")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_AUSTRALIAN_E})}
                        formErrors={errMsgsAus}
                    />
                    <CustomTextField
                        placeholder="Street Number"
                        id="sec-e-street-no"
                        label="Street Number"
                        asterisk=""
                        name="street_no"
                        value={getValueAddressType(ADDRESS_TYPE_AUSTRALIAN_E, "street_no")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_AUSTRALIAN_E})}
                        formErrors={errMsgsAus}
                    />
                    <CustomTextField
                        placeholder="Street name"
                        id="sec-e-street-name"
                        label="Street name"
                        asterisk=""
                        name="street_name"
                        value={getValueAddressType(ADDRESS_TYPE_AUSTRALIAN_E, "street_name")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_AUSTRALIAN_E})}
                        formErrors={errMsgsAus}
                    />
                    <CustomTextField 
                        id="sec-e-suburb"
                        placeholder="Suburb/Town"
                        label="Suburb/Town"
                        asterisk=""
                        name="suburb"
                        value={getValueAddressType(ADDRESS_TYPE_AUSTRALIAN_E, "suburb")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_AUSTRALIAN_E})}
                        formErrors={errMsgsAus}
                    />
                    <CustomTextField
                        id="sec-e-state"
                        placeholder="State"
                        label="State"
                        asterisk=""
                        name="state"
                        value={getValueAddressType(ADDRESS_TYPE_AUSTRALIAN_E, "state")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_AUSTRALIAN_E})}
                        formErrors={errMsgsAus}
                    />
                    <CustomTextField
                        id="sec-e-postcode"
                        placeholder="Postcode"
                        label="Postcode"
                        asterisk=""
                        name="postcode"
                        value={getValueAddressType(ADDRESS_TYPE_AUSTRALIAN_E, "postcode")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_AUSTRALIAN_E})}
                        formErrors={errMsgsAus}
                    />

                    <div className="country__dropdown u-margin-bottom-3">
                        <label>Country</label>
                        <Select
                            className={errMsgsAus?.countrySub?.length && 'incorrect'}
                            id="select-dropdown-country"
                            name="countrySub"
                            autoFocus={false}
                            options={options}
                            placeholder="Select a Country"
                            value={countryValue(ADDRESS_TYPE_AUSTRALIAN_E)}
                            onChange={(value) => onChangeAddress({ name:'country', value: value.label, addressType: ADDRESS_TYPE_AUSTRALIAN_E })}
                        />

                        {errMsgsAus?.countrySub?.length?
                        <div className="input__desc">
                            {renderErrorTxt(errMsgsAus?.countrySub)}
                        </div>: null}
                    </div>

                </div>
                <div  className="u-padding-10y">
                    <CustomTextField
                        id="sec-e-reg-office-address"
                        label="Registered office address"
                        placeholder="Registered office address"
                        asterisk=""
                        type="form-input-full-width"
                        name="full_company_address"
                        value={getValueAddressType(ADDRESS_TYPE_ASIC_REGISTERED_E, "full_company_address")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_ASIC_REGISTERED_E})}
                        formErrors={errMsgsAsic}
                    />
                </div>
                <div className="sub__form">
                    <CustomTextField
                        id="sec-e-reg-unit-no"
                        label="Unit No./Level"
                        placeholder="Unit No./Level"
                        asterisk=""
                        name="unit_no"
                        value={getValueAddressType(ADDRESS_TYPE_ASIC_REGISTERED_E, "unit_no")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_ASIC_REGISTERED_E})}
                        formErrors={errMsgsAsic}
                    />
                    <CustomTextField
                        id="sec-e-reg-street-no"
                        label="Street Number"
                        placeholder="Street Number"
                        asterisk=""
                        name="street_no"
                        value={getValueAddressType(ADDRESS_TYPE_ASIC_REGISTERED_E, "street_no")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_ASIC_REGISTERED_E})}
                        formErrors={errMsgsAsic}
                    />
                    <CustomTextField
                        id="sec-e-reg-street-name"
                        label="Street name"
                        placeholder="Street name"
                        asterisk=""
                        name="street_name"
                        value={getValueAddressType(ADDRESS_TYPE_ASIC_REGISTERED_E, "street_name")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_ASIC_REGISTERED_E})}
                        formErrors={errMsgsAsic}
                    />
                    <CustomTextField
                        id="sec-e-reg-suburb"
                        label="Suburb/Town"
                        placeholder="Suburb/Town"
                        asterisk=""
                        name="suburb"
                        value={getValueAddressType(ADDRESS_TYPE_ASIC_REGISTERED_E, "suburb")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_ASIC_REGISTERED_E})}
                        formErrors={errMsgsAsic}
                    />
                    <CustomTextField
                        id="sec-e-reg-state"
                        label="State"
                        placeholder="State"
                        asterisk=""
                        name="state"
                        value={getValueAddressType(ADDRESS_TYPE_ASIC_REGISTERED_E, "state")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_ASIC_REGISTERED_E})}
                        formErrors={errMsgsAsic}
                    />
                    <CustomTextField
                        id="sec-e-reg-postcode"
                        label="Postcode"
                        placeholder="Postcode"
                        asterisk=""
                        name="postcode"
                        value={getValueAddressType(ADDRESS_TYPE_ASIC_REGISTERED_E, "postcode")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_ASIC_REGISTERED_E})}
                        formErrors={errMsgsAsic}
                    />

                    <div className="country__dropdown u-margin-bottom-3">
                        <label>Country</label>
                        <Select
                            className={errMsgsAsic?.countrySub?.length && 'incorrect'}
                            id="select-dropdown-country"
                            name="countrySub"
                            autoFocus={false}
                            options={options}
                            placeholder="Select a Country"
                            value={countryValue(ADDRESS_TYPE_ASIC_REGISTERED_E)}
                            onChange={(value) => onChangeAddress({ name:'country', value: value.label, addressType: ADDRESS_TYPE_ASIC_REGISTERED_E })}
                        />

                        {errMsgsAsic?.countrySub?.length?
                        <div className="input__desc">
                            {renderErrorTxt(errMsgsAsic?.countrySub)}
                        </div>: null}
                    </div>
                </div>
            </div>

            {/* Is the company also registered with a relevant foreign registration body? */}
            <div className="u-padding-10y">
                <h6>Is the company also registered with a relevant foreign registration body?</h6>
                <div>
                    <div className="form-input-radio">
                        <input
                            type="radio"
                            name="is_registered_with_relevant_foreign_registration_body"
                            id="sec-e-reg-foreign-body-yes"
                            value={YES_E}
                            checked={secE?.is_registered_with_relevant_foreign_registration_body === YES_E}
                            onChange={(e)=>onChangeRadioFn({name:e.target.name, value:e.target.value})}
                        />
                        <label htmlFor="sec-e-reg-foreign-body-yes">Yes</label>
                        {secE?.is_registered_with_relevant_foreign_registration_body === YES_E? <RegistrationBodyInp onChangeFn={onChangeFn} secE={secE} formErrors={invFormStatus.errors}/>: null}
                    </div>
                    <CustomRadioInput
                        name="is_registered_with_relevant_foreign_registration_body"
                        id="sec-e-reg-foreign-body-no"
                        content="No"
                        val={NO_E}
                        checked={secE?.is_registered_with_relevant_foreign_registration_body === NO_E}
                        onChangeFn={onChangeRadioFn}
                    />
                    {invFormStatus.errors?.is_registered_with_relevant_foreign_registration_body? <div className="input__desc">
                        {renderErrorTxt(invFormStatus.errors?.is_registered_with_relevant_foreign_registration_body)}
                    </div>: null} 
                </div>
            </div>

            {/* Complete this section if the company is NOT registered with ASIC */}
            <div className="u-padding-10y">
                <h5 className="u-padding-y">Complete this section if the company is NOT registered with ASIC</h5>

                <div className="u-margin-bottom">
                    <h6>Is the company registered with a relevant foreign registration body?</h6>

                    <div className="form-input-radio">
                        <input
                            type="radio"
                            name="is_registered_with_relevant_foreign_registration_body_no_asic"
                            id="sec-e-not-asic-foreign-yes"
                            value={YES_E}
                            checked={secE?.is_registered_with_relevant_foreign_registration_body_no_asic === YES_E}
                            onChange={(e)=>onChangeRadioFn({name:e.target.name, value:e.target.value})}
                            // onChange={(e)=>onChangeRadioFn({name:"is_registered_with_relevant_foreign_registration_body", value:e.target.value})}
                        />
                        <label htmlFor="sec-e-not-asic-foreign-yes">Yes</label>

                        {secE?.is_registered_with_relevant_foreign_registration_body_no_asic === YES_E? <NonAsicIdentNum secE={secE} onChangeFn={onChangeFn} formErrors={invFormStatus.errors}/>: null}
                    </div>

                    <CustomRadioInput
                        name="is_registered_with_relevant_foreign_registration_body_no_asic"
                        id="sec-e-not-asic-foreign-no"
                        content="No"
                        val={NO_E}
                        checked={secE?.is_registered_with_relevant_foreign_registration_body_no_asic === NO_E}
                        onChangeFn={onChangeRadioFn}
                        // onChangeFn={({name, value})=>onChangeRadioFn({name:"is_registered_with_relevant_foreign_registration_body", value})}
                    />
                    {invFormStatus.errors?.is_registered_with_relevant_foreign_registration_body_no_asic? <div className="input__desc">
                        {renderErrorTxt(invFormStatus.errors?.is_registered_with_relevant_foreign_registration_body_no_asic)}
                    </div>: null}
                </div>

                <div className="u-padding-y">
                    <CustomTextField
                        placeholder="Full address of the company as registered by the foreign registration body"
                        id="sec-e-full-address-not-asic"
                        label="Full address of the company in the country of formation, incorporation or registration as registered by the foreign registration body (PO Box not permitted)"
                        type="form-input-full-width"
                        asterisk=""
                        name="full_company_address"
                        value={getValueAddressType(ADDRESS_TYPE_NON_ASIC_REGISTERED_E, "full_company_address")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_NON_ASIC_REGISTERED_E})}
                        formErrors={errMsgsNonAsic}
                        />
                </div>
                <div className="sub__form">
                    <CustomTextField
                        placeholder="Unit No./Level"
                        id="sec-e-unit-not-asic"
                        label="Unit No./Level"
                        asterisk=""
                        name="unit_no"
                        value={getValueAddressType(ADDRESS_TYPE_NON_ASIC_REGISTERED_E, "unit_no")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_NON_ASIC_REGISTERED_E})}
                        formErrors={errMsgsNonAsic}
                    />
                    <CustomTextField
                        placeholder="Street Number"
                        id="sec-e-street-no-not-asic"
                        label="Street Number"
                        asterisk=""
                        name="street_no"
                        value={getValueAddressType(ADDRESS_TYPE_NON_ASIC_REGISTERED_E, "street_no")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_NON_ASIC_REGISTERED_E})}
                        formErrors={errMsgsNonAsic}
                    />
                    <CustomTextField
                        placeholder="Street name"
                        id="sec-e-street-not-asic"
                        label="Street name"
                        asterisk=""
                        name="street_name"
                        value={getValueAddressType(ADDRESS_TYPE_NON_ASIC_REGISTERED_E, "street_name")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_NON_ASIC_REGISTERED_E})}
                        formErrors={errMsgsNonAsic}
                    />

                    <CustomTextField
                        placeholder="Suburb/Town"
                        id="sec-e-suburb-not-asic"
                        label="Suburb/Town"
                        asterisk=""
                        name="suburb"
                        value={getValueAddressType(ADDRESS_TYPE_NON_ASIC_REGISTERED_E, "suburb")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_NON_ASIC_REGISTERED_E})}
                        formErrors={errMsgsNonAsic}
                    />
                    <CustomTextField
                        placeholder="State"
                        id="sec-e-state-not-asic"
                        label="State"
                        asterisk=""
                        name="state"
                        value={getValueAddressType(ADDRESS_TYPE_NON_ASIC_REGISTERED_E, "state")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_NON_ASIC_REGISTERED_E})}
                        formErrors={errMsgsNonAsic}
                    />
                    <CustomTextField
                        placeholder="Postcode"
                        id="sec-e-postcode-not-asic"
                        label="Postcode"
                        asterisk=""
                        name="postcode"
                        value={getValueAddressType(ADDRESS_TYPE_NON_ASIC_REGISTERED_E, "postcode")}
                        onChangeFn={({name, value})=>onChangeAddress({name, value, addressType:ADDRESS_TYPE_NON_ASIC_REGISTERED_E})}
                        formErrors={errMsgsNonAsic}
                    />

                    <div className="country__dropdown">
                        <label>Country</label>
                        <Select
                            className={errMsgsNonAsic?.countrySub?.length && 'incorrect'}
                            id="select-dropdown-country"
                            name="countrySub"
                            autoFocus={false}
                            options={options}
                            placeholder="Select a Country"
                            value={countryValue(ADDRESS_TYPE_NON_ASIC_REGISTERED_E)}
                            onChange={(value) => onChangeAddress({ name:'country', value: value.label, addressType: ADDRESS_TYPE_NON_ASIC_REGISTERED_E })}
                        />

                        {errMsgsNonAsic?.countrySub?.length?
                        <div className="input__desc">
                            {renderErrorTxt(errMsgsNonAsic?.countrySub)}
                        </div>: null}
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default ForeignCompanyDetails;