import React, {useEffect} from 'react';
import { renderErrorTxt } from '../../../../utils/validations/Investor-Application-Form/formErrorUtils';

function RegistrationBodyInp({onChangeFn, secE, formErrors}) {

    useEffect(()=>{
        return ()=>{
            onChangeFn({name:"relevant_foreign_registration_body", value:""})    
        }
    },[])

    const onChangeIn = (e) => {
        const {name, value} = e.target;
        onChangeFn({name, value})
    }

  return (
    <>
    <input className="form-input-common" type="text" placeholder="Name of registration body" id="sec-e-name-reg-body" name="relevant_foreign_registration_body" value={secE?.relevant_foreign_registration_body || ""} onChange={onChangeIn} />
    {formErrors?.relevant_foreign_registration_body? <div className="input__desc">
        {renderErrorTxt(formErrors?.relevant_foreign_registration_body)}
    </div>: null}
    </>
  )
}

export default RegistrationBodyInp