import { useSelector } from 'react-redux';
import CustomTextField from '../../Layout/CustomTextField';

const Directors = ({ directorId, details, onChangeDirectorDetails, itemIndex }) => {
    const invFormStatus = useSelector((store) => store.invFormStatus)

    const errMsgs = {
        given_name : invFormStatus.errors?.given_name? invFormStatus.errors?.given_name[itemIndex]: [],
        surname : invFormStatus.errors?.surname? invFormStatus.errors?.surname[itemIndex]: [],
        middle_name : invFormStatus.errors?.middle_name? invFormStatus.errors?.middle_name[itemIndex]: [],
    }
    
    const onChangeFn = ({name, value}) => {
        onChangeDirectorDetails({name, value, directorId})
    }

    return (
    <div key={directorId}>
        <h6 className="u-padding-10y">{`Director ${itemIndex+1}`}</h6>
            <div className="sub__form">
                <CustomTextField
                    placeholder="Given Name"
                    id={`sec-e-director${directorId}-given-name`}
                    label="Given Name"
                    asterisk="*"
                    name="given_name"
                    value={details?.given_name || ""}
                    onChangeFn={onChangeFn}
                    formErrors={errMsgs}
                />
                <CustomTextField
                    placeholder="Surname"
                    id={`sec-e-director${directorId}-surname`}
                    label="Surname"
                    asterisk="*"
                    name="surname"
                    value={details?.surname || ""}
                    onChangeFn={onChangeFn}
                    formErrors={errMsgs}
                /> 
            </div>
    </div>
    )

}

export default Directors;