import React from 'react';
import { useSelector } from 'react-redux';
import TaxInformation from './SubSections/TaxInformation';
import RegulatoryDetails from './SubSections/RegulatoryDetails';
import ForeignCompanyDetails from './SubSections/ForeignCompanyDetails';
import IdentificationDocs from './SubSections/IdentificationDocs';
import DirectorsSection from './SubSections/DirectorsSection';
import CompanyType from './SubSections/CompanyType';
import {formatAndPrintDocs} from '../../../../utils/validations/helperFunc';

const SectionEContent = ({ onFormChange, investorFormId, removeDocFromArr }) => {

  const secE = useSelector((store) => store.secE);

  return (
    <div className="section">
        <h2 className="section__heading">Section E: Foreign Company</h2>
        <ul>
            <li>Section E is to be completed by foreign companies. Please complete Section D for Australian companies. Please use BLOCK letters to complete this Application Form.</li>
        </ul>

        <ForeignCompanyDetails onFormChange={onFormChange}/>

        <CompanyType onFormChange={onFormChange}/>

        <DirectorsSection onFormChange={onFormChange} investorFormId={investorFormId} />

        <RegulatoryDetails onFormChange={onFormChange}/>

        {/* <TaxInformation /> */}

        <IdentificationDocs investorFormId={investorFormId}/>

        {secE?.documents?.length > 0? formatAndPrintDocs(secE?.documents, removeDocFromArr): null}
    </div>
  )
}

export default SectionEContent;