import { useSelector } from 'react-redux';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import PublicListedComp from './subRegulatory/PublicListedComp';
import OwnedListedComp from './subRegulatory/OwnedListedComp';
import RegulatedComp from './subRegulatory/RegulatedComp';
import { COMPANY_IS_NOT_REGULATED_OR_LISTED_E, PUBLIC_LISTED_COMPANY_E, MAJORITY_OWNED_SUBSIDIARY_OF_A_LISTED_COMPANY_E, REGULATED_COMPANY_E } from '../../../../../constants';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';

const RegulatoryDetails = ({onFormChange}) => {
    const secE = useSelector((store)=> store.secE);
    const invFormStatus = useSelector((store) => store.invFormStatus)

    const onChangeFn = ({name, value}) => {
        onFormChange({name, value, level:"regulatory_detail"})
    }

    const onChangeRadioFn = ({name, value}) =>{ 
        const convrtToNum = parseInt(value);
        onFormChange({name, value:convrtToNum, level:"regulatory_detail"});
    }

  return (
    <div className="section__container">
        <h4 className="section__container--subtitle">E.4 Regulatory / Listing Details (All Companies To Complete)</h4>

        <div className="section__container--box">
            <h6>Is the company regulated or listed?<span>*</span></h6>

            <div className="">
                <CustomRadioInput
                    name="is_company_regulated"
                    id="sec-e-company-not-regulated"
                    content="Company is not regulated or listed"
                    val={COMPANY_IS_NOT_REGULATED_OR_LISTED_E}
                    onChangeFn={onChangeRadioFn}
                    checked={secE.regulatory_detail?.is_company_regulated === COMPANY_IS_NOT_REGULATED_OR_LISTED_E}
                />
                <CustomRadioInput
                    name="is_company_regulated"
                    id="sec-e-public-listed"
                    content="Public listed company"
                    val={PUBLIC_LISTED_COMPANY_E}
                    onChangeFn={onChangeRadioFn}
                    checked={secE.regulatory_detail?.is_company_regulated === PUBLIC_LISTED_COMPANY_E}
                />

                {/* public listed input fields  */}
                {secE.regulatory_detail?.is_company_regulated === PUBLIC_LISTED_COMPANY_E? <PublicListedComp secE={secE} onChangeFn={onChangeFn}/>: null }

                <CustomRadioInput
                name="is_company_regulated"
                id="sec-e-major-owned-subsidiary"
                content="Majority-owned subsidiary of a listed company"
                val={MAJORITY_OWNED_SUBSIDIARY_OF_A_LISTED_COMPANY_E}
                onChangeFn={onChangeRadioFn}
                checked={secE.regulatory_detail?.is_company_regulated === MAJORITY_OWNED_SUBSIDIARY_OF_A_LISTED_COMPANY_E}
                />

                {/* major owned subsidiary inputs */}
                {secE.regulatory_detail?.is_company_regulated === MAJORITY_OWNED_SUBSIDIARY_OF_A_LISTED_COMPANY_E? <OwnedListedComp secE={secE} onChangeFn={onChangeFn}/>: null }

                <CustomRadioInput
                name="is_company_regulated"
                id="sec-e-company-regulated"
                content="Regulated company - A company whose activities are subject to the oversight of a Commonwealth, State or Territory statutory regulator. In this context Regulated means subject to supervision beyond that provided by ASIC as a company registration body. For example, Australian Financial Services Licensee (AFSL), Australian Credit Licensee (ACL) or Registrable Superannuation Entity (RSE) Licensee"
                type="row-input"
                val={REGULATED_COMPANY_E}
                onChangeFn={onChangeRadioFn}
                checked={secE.regulatory_detail?.is_company_regulated === REGULATED_COMPANY_E}
                />

                {/* regulated company input fields */}
                {secE.regulatory_detail?.is_company_regulated === REGULATED_COMPANY_E? <RegulatedComp secE={secE} onChangeFn={onChangeFn}/> : null }
            </div>
            {invFormStatus.errors?.is_company_regulated? <div className="input__desc">
            {renderErrorTxt(invFormStatus.errors?.is_company_regulated)}
        </div>: null}
        </div>
    </div>
  )
}

export default RegulatoryDetails;