import React from 'react';
import { useSelector } from 'react-redux';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import Directors from '../Directors';
import { singleDirectorDetail } from '../../../../../reducers/Investor-Application-Form/secEReducer';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import MoreThanFourDirectors from '../MoreThanFourDirectors';
import { NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E } from '../../../../../constants';

const DirectorsSection = ({ onFormChange, investorFormId }) => {
    const secE = useSelector((store)=> store.secE);
    const invFormStatus = useSelector((store) => store.invFormStatus)

    const addDirectorDetails = (noOfDirectors) => {
        let upd = []
        const prevDirLen = secE?.directors_details?.length || 0
        if(prevDirLen > noOfDirectors){
            upd = secE?.directors_details?.filter((each, index)=> (index+1)<=noOfDirectors);
            onFormChange({name:"directors_details", value:[...upd]});
            return
        } else if(prevDirLen === noOfDirectors) {
            return
        } else {
            const newLen = noOfDirectors-prevDirLen
            upd = [...secE?.directors_details]
            const newArr = Array.from({length:newLen}, ()=>({...singleDirectorDetail}))
            onFormChange({name:"directors_details", value:[...upd, ...newArr]})
            return
        }
    }

    const onChangeRadioFn = ({name, value}) =>{ 
        const convrtToNum = parseInt(value);
        onFormChange({
            name,
            value: convrtToNum
        });

        if(convrtToNum > 0){
            //update no.of directors
            addDirectorDetails(convrtToNum);
        }
        else if(convrtToNum === NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E){
            addDirectorDetails(0);
        }
        
    }

    const onChangeDirectorDetails = ({directorId, name, value}) => {
        const updIndex = directorId - 1;
        const upd = [...secE?.directors_details];
        upd[updIndex][name] = value
        onFormChange({name:"directors_details", value:[...upd]});
        return
    }

    const renderDirectors = () => {
        return secE?.directors_details?.map((each, index)=> {
            const tempId = index+1
            const uniqId = tempId //each?.id || tempId
            if((index < secE?.number_of_directors) && (secE?.number_of_directors !== NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E)){ //phase1 fix. remove this condition in phase-2
                return <Directors key={uniqId} directorId={uniqId} details={each} onChangeDirectorDetails={onChangeDirectorDetails} itemIndex={index}/>
            }
            return null
        })
    }

  return (
    <div className="section__container">
        <h4 className="section__container--subtitle">E.3 Directors (Proprietary Companies Only)</h4>
        <div className="section__container--box">
            <h6>How many directors are there?<span>*</span></h6>
            <div className="u-margin-bottom row-space-between">
                <CustomRadioInput
                    name="number_of_directors"
                    id="sec-e-director-1"
                    content="1"
                    val="1"
                    checked={secE?.number_of_directors === 1}
                    onChangeFn={onChangeRadioFn}            
                />
                <CustomRadioInput
                    name="number_of_directors"
                    id="sec-e-director-2"
                    content="2"
                    val="2"
                    checked={secE?.number_of_directors === 2}
                    onChangeFn={onChangeRadioFn}
                />
                <CustomRadioInput
                    name="number_of_directors"
                    id="sec-e-director-3"
                    content="3"
                    val="3"
                    checked={secE?.number_of_directors === 3}
                    onChangeFn={onChangeRadioFn}
                />
                <CustomRadioInput
                    name="number_of_directors"
                    id="sec-e-director-4"
                    content="4"
                    val="4"
                    checked={secE?.number_of_directors === 4}
                    onChangeFn={onChangeRadioFn}
                />
                <CustomRadioInput
                    name="number_of_directors"
                    id="sec-e-director-5"
                    content="more than 4" 
                    val={`${NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E}`}
                    checked={secE?.number_of_directors === NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E}
                    onChangeFn={onChangeRadioFn}
                />
            </div>
            <React.Fragment>
                {invFormStatus.errors?.number_of_directors? <div className="input__desc">
                        {renderErrorTxt(invFormStatus.errors?.number_of_directors)}
                    </div>: null}

                    {secE?.number_of_directors === NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E?
                        <>
                            <h6 className="u-padding-y">If there are more directors, please provide details on separate sheet and upload</h6>
                            <MoreThanFourDirectors investorFormId={investorFormId}/>
                        </>
                    : null}
            </React.Fragment>
            {renderDirectors()}
        </div>
    </div>
  )
}

export default DirectorsSection;