import React,{useEffect}  from 'react'
import { renderErrorTxt } from '../../../../utils/validations/Investor-Application-Form/formErrorUtils';

function NonAsicIdentNum({secE, onChangeFn, formErrors}) {

    useEffect(()=>{
        return ()=>{
            onChangeFn({name:"foreign_registration_body_identification", value:""})    
        }
    },[])

    const onChangeIn = (e) => {
        const {name, value} = e.target;
        onChangeFn({name, value})
    }

  return (
    <>
        <div className="input__desc full-width">
            <p className="input__desc--p">If Yes, please provide the company identification number (if any) that was issued by the relevant foreign body upon the company's formation, incorporation or regulation</p>
        </div>
        <div className="form-input form-input-full-width">
            <input className="form-input-common" type="text" placeholder="Company identification number" id="sec-e-company-id" name="foreign_registration_body_identification" value={secE?.foreign_registration_body_identification || ""} onChange={onChangeIn}/>
        </div>
        {formErrors?.foreign_registration_body_identification? <div className="input__desc">
            {renderErrorTxt(formErrors?.foreign_registration_body_identification)}
        </div>: null}
    </>
  )
}

export default NonAsicIdentNum