import React, { useEffect, useMemo } from 'react';
import Select from 'react-select';
import { useSelector } from 'react-redux';
import CustomTextField from '../../../../Layout/CustomTextField';
import countryList from 'react-select-country-list';
import { renderErrorTxt } from '../../../../../../utils/validations/Investor-Application-Form/formErrorUtils';

function OwnedListedComp({ secE, onChangeFn }) {

    const invFormStatus = useSelector((store) => store.invFormStatus);

    const options = useMemo(() => countryList().getData(), []);

    useEffect(()=>{
        return ()=> {
            onChangeFn({name:'majority_owned_listed_company_name', value:''})
            onChangeFn({name:'majority_owned_name_of_market', value:''})
            onChangeFn({name:'majority_owned_country', value:''})
        }
    },[]);

    const countryValue = () => {
        const value = options.find((country) => {
            if(secE?.regulatory_detail?.majority_owned_country){
              return  country.label?.toLowerCase() === secE?.regulatory_detail?.majority_owned_country.toLowerCase()
            }
            return false
        });
        return value || null;
    }

    const onChangeSelectHandler = (value) => {
        onChangeFn({ name: 'majority_owned_country', value: value.label })
    }

    return (
    <div className="u-padding-y">
            <CustomTextField 
                placeholder="Listed company name"
                id="sec-e-major-listed-name"
                label="Listed company name"
                type="form-input-full-width"
                asterisk="*"
                name="majority_owned_listed_company_name"
                value={secE.regulatory_detail?.majority_owned_listed_company_name || ""}
                onChangeFn={onChangeFn}
                formErrors={invFormStatus.errors}
            />
            <div className="sub__form">
                <CustomTextField
                    placeholder="Name of market / exchange"
                    id="sec-e-major-owned-name"
                    label="Name of market / exchange"
                    asterisk="*"
                    name="majority_owned_name_of_market"
                    value={secE.regulatory_detail?.majority_owned_name_of_market || ""}
                    onChangeFn={onChangeFn}
                    formErrors={invFormStatus.errors}
                />

                <div className="country__dropdown">
                    <label>Country <span>*</span></label>
                    <Select
                        className={invFormStatus.errors?.majority_owned_country && 'incorrect'}
                        id="select-dropdown-country"
                        name="majority_owned_country"
                        autoFocus={false}
                        options={options}
                        placeholder="Select a Country"
                        value={countryValue()}
                        onChange={onChangeSelectHandler}
                    />

                    {invFormStatus.errors?.majority_owned_country?
                    <div className="input__desc">
                        {renderErrorTxt(invFormStatus.errors?.majority_owned_country)}
                    </div>: null}
                </div>
            </div>
        </div>
  )
}

export default OwnedListedComp